        <div class="left2">
          <div class="left2title">设备效率</div>
          <div class="left2content" id="echarts1" ref="echarts1"></div>
          <div class="line"></div>
        </div>
        <div class="left3">
          <div class="left3title">设备管理</div>
          <div class="left3content">
            <div class="left3_1">
              <div>
                <p class="numFLag">{{ repairCard.no }}</p>
                <p class="titleFlag">未开始</p>
              </div>
              <div>
                <p class="numFLag">{{ repairCard.do }}</p>
