              <div class="title">维修任务统计</div>
            </div>
            <div class="left3_2">
              <div>
                <p class="numFLag">{{ maintainCard.no }}</p>
                <p class="titleFlag">未开始</p>
              </div>
              <div>
                <p class="numFLag">{{ maintainCard.do }}</p>
