              </div>
              <div>
                <p class="numFLag">{{ checkCard.do }}</p>
                <p class="titleFlag">点检中</p>
              </div>
              <div>
                <p class="numFLag">{{ checkCard.off }}</p>
                <p class="titleFlag">已完成</p>
              </div>
              <div class="title">点检任务统计</div>
            </div>
          </div>
          <div class="line"></div>
        </div>
      </div>
      <div class="rightContent">
        <div class="right1">
          <div class="right1title">设备分布</div>
          <distributes></distributes>
          <!-- <div class="devLegen">
            <div class="yxLege"></div>
            <div class="legeTitle">运行</div>
            <div class="kxLege"></div>
            <div class="legeTitle">空闲</div>
            <div class="lxLege"></div>
            <div class="legeTitle">离线</div>
            <div class="gjLege"></div>
            <div class="legeTitle">告警</div>
          </div>
         <div class="right1content" id="right1content" ref="right1content" v-html="devStateHtml"></div> -->
        </div>
        <div class="right2">
          <div class="right2title">今日设备状态</div>
          <div class="right2content" id="echarts4" ref="echarts4"></div>
          <div class="line"></div>
        </div>
        <div class="right3" v-if="!kanbanConfig.showEnergy"  >
          <div class="right3title">设备预警</div>
          <div class="right3content">
            <table id="warnTable" >
              <tr v-for="(item, index) in warnList" :key="index" >
                <td><div class="warnRed" ></div></td>
                <td>
                  <p class="active" >{{item.productionLine}}  {{item.deviceName}}   于  {{item.warningStartTime}} 告警</p>
                </td>
              </tr>
            </table>
          </div>
          <div class="line"></div>
        </div>
        <div class="right4" v-if="kanbanConfig.showEnergy" >
          <div class="right4title">
            电表统计
            <div class="cardContent" >
              <div class="card" ><p>今日总用量：{{todayElecticeNum}} KW·H</p></div>
            </div>
          </div>
          <div class="right4content"  ref="right4content"  >
            <div class="eleTask" v-for="(item, index) in electricList" :key="index" >
              <p>{{item.deviceName}} : <span>{{item.dayEnergyConsumption}} KW·H</span></p>
            </div>
          </div>
          <div class="line"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import distributes from '@/views/kanban_bsb/distributes.vue';
// 导入所需接口
import {
  deviceRealLiveGetDeviceRealState,
  deviceRealLiveGetDeviceStateReportStatistics,
  deviceManagerGetCheckCount,
  deviceManagerGetRepairTaskCount,
  deviceManagerGetMaintainTaskCount,
  deviceReportGetDeviceDayUtilization,
  deviceEarlyWarningGetDeviceWarnRecord,
  deviceRealLiveGetPageByDevice,
  getEnergyTodayUse,
  getEnergyTodayAndWeek
} from '@/api/kanban';
import {
  STATUS_CODE
  // TITLE
} from '@/utils/constantsFile';
import {
// showErrorNotify
} from '@/utils/notify';
// 导入通用方法
import {
  // getGroupId,
  getTodayFirstDay,
  getTodayLastDay,
  getSearchToday
} from '@/utils/publicFunction2';

export default {
  name: 'kanban',
  components: {
    // 加载未审核组件
    distributes
  },
  data() {
    return {
      title: '',
      groupId: 0,
      timer: {},
      timer4: {},
      time: '',
      warnHtml: '',
      devStateHtml: '',
      UtilizNum: 0,
      devList: [],
      devLbIndex: 0,
      warnList: [],
      // 设备分布数据
      productTaskData: [],
      // 生产任务 每行的展示的设备数
      rowDeviceNum: 10,
      repairCard: {
        no: 0,
        do: 0,
        off: 0
      },
      maintainCard: {
        no: 0,
        do: 0,
        off: 0
      },
      checkCard: {
        no: 0,
        do: 0,
        off: 0
      },
      current: 1,
      size: 6,
      current_w: 1,
      size_w: 4,
      kanbanConfig: { title: '数字化车间看板', key: '', time: 15000 },
      electricList: [],
      todayElecticeNum: 0
    };
  },
  mounted() {
    // 拿到表格挂载后的真实DOM
    const table = this.$refs.right1content;
    console.log(table.right1content);
    // 拿到表格中承载数据的div元素
    const divData = table;
    // // 拿到元素后，对元素进行定时增加距离顶部距离，实现滚动效果(此配置为每100毫秒移动1像素)
    this.finga(divData);

    if (this.kanbanConfig.showEnergy) {
      const table4 = this.$refs.right4content;
      const divData4 = table4;
      this.finga4(divData4);
    }
  },
  created() {
    var that = this;

    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }

    console.log(this.getUrlKeyMain('tvNo', window.location.href));
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
    } else {
      this.title = that.kanbanConfig.title;
    }

    // this.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];

    // setInterval(() => {
    //   that.tick();
    // }, 1000);
    this.getDataTime();

    this.$nextTick(() => {
      this.showLineAndDev(); // 设备分布
      this.init();
    });

    setInterval(() => {
      this.showLineAndDev(); // 设备分布
    }, 5000);

    if (that.kanbanConfig) {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, that.kanbanConfig.time);
      }, 0);
    } else {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, 15000);
      }, 0);
    }
  },
  methods: {
    init() {
      this.showEchartsData(); // 设备效率
      this.showEcharts2Data(); // 今日设备状态分布
      this.showEcharts3Data(); // 设备利用率
      this.showEcharts4Data(); // 已对接
      this.showDevManagerData(); // 设备管理

      if (this.kanbanConfig.showEnergy) {
        this.showElectric(); // 电表数据
      } else {
        this.showWarn(); // 设备预警
      }
    },
    finga(divData) {
      this.timer = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    finga4(divData) {
      this.timer4 = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer4);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga4(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    async showElectric() {
      try {
        var condtion = {
          current: 1,
          size: 99999999
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayUse({ energyEnum: 'ELECTRICITY' }, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.electricList = res.rows;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
      try {
        // eslint-disable-next-line no-redeclare
        var condtion = {};
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await getEnergyTodayAndWeek({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.todayElecticeNum = res.rows.electricityStatistics.dayEnergyConsumption;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEchartsData() {
      if (this.devList.length === 0 || this.devLbIndex === this.devList.length - 1) {
        try {
          var condtion = {};
          console.log('7788', localStorage.getItem('companyId'));
          if (this.getUrlKeyMain('cid', window.location.href)) {
            condtion.secretKey = this.kanbanConfig.key;
          }
          if (this.groupId > 0) {
            condtion.conditionJson1 = [
              {
                column: 'groupId',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: this.groupId
              }
            ];
          }

          const res = await deviceRealLiveGetDeviceStateReportStatistics({
            current: 1,
            size: 99999,
            startTime: getTodayFirstDay(),
            endTime: getTodayLastDay()
          }, condtion
          );
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            console.log(333, res);
            this.devList = res.rows;
            this.getDeviceTimeRecentDayUtilization();
            this.devLbIndex = 0;
          }
        } catch (error) {
          console.log(error);
        }
      } else {
        this.getDeviceTimeRecentDayUtilization();
        this.devLbIndex++;
      }
    },
    async getDeviceTimeRecentDayUtilization() {
      try {
        var condtion = {
          conditionJson1: [
            {
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'eq',
              value: this.devList[this.devLbIndex].deviceCode
            }
          ]
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        console.log('lalal', condtion);
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: getSearchToday(7) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59'
        },
        condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log('你好', res);
          var sbxl = [];
          var pjxl = [];
          var time = [];
          var all = [];
          var numAll = 0;
          for (var i = 0; i < res.rows.length; i++) {
            // pjxl.push(Number((data.dataset * 100).toFixed(2)));
            sbxl.push(Number((res.rows[i].value * 100).toFixed(2)));
            time.push(this.formatDateThree(res.rows[i].time));
            all.push(Number((res.rows[i].value * 100).toFixed(2)));
            numAll += res.rows[i].value;
          }
          var avg = ((numAll / all.length) * 100).toFixed(2);
          for (var x = 0; x < all.length; x++) {
            pjxl.push(Number(avg));
          }
          console.log('大家好', pjxl);
          this.showEcharts(sbxl, pjxl, time, Math.min.apply(null, all) - 5, Math.max.apply(null, all) + 5, this.devList[this.devLbIndex].deviceName);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showDevManagerData() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceManagerGetCheckCount({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows.waitCount === undefined) {
            this.checkCard.no = 0;
            this.checkCard.do = 0;
            this.checkCard.off = 0;
          } else {
            this.checkCard.no = res.rows.waitCount;
            this.checkCard.do = res.rows.ingCount;
            this.checkCard.off = res.rows.finishCount;
          }
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }

      try {
        const res = await deviceManagerGetRepairTaskCount({},
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.repairCard.no = res.rows.waitCount;
          this.repairCard.do = res.rows.ingCount;
          this.repairCard.off = res.rows.finishCount;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }

      try {
        const res = await deviceManagerGetMaintainTaskCount({},
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainCard.no = res.rows.waitCount;
          this.maintainCard.do = res.rows.ingCount;
          this.maintainCard.off = res.rows.finishCount;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts2Data() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceRealLiveGetDeviceRealState({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showEcharts2(
            [
              { name: '运行', value: res.rows.runCount },
              { name: '空闲', value: res.rows.idleCount },
              { name: '离线', value: res.rows.offLineCount },
              { name: '告警', value: res.rows.warnCount }
            ]
          );
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts3Data() {
      var condtion = {
        conditionJson1: []
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var num = parseInt(res.rows[0].value * 100);
          this.showEcharts3(num);
          this.UtilizNum = num;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts4Data() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceRealLiveGetDeviceStateReportStatistics({
          current: this.current,
          size: this.size,
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current * this.size >= res.total) {
            this.current = 1;
          } else {
            this.current++;
          }

          var run = [];
          var free = [];
          var noline = [];
          var warn = [];
          var name = [];
          for (var i = 0; i < res.rows.length; i++) {
            var all = res.rows[i].deviceLiveReportVo.runTimeSum + res.rows[i].deviceLiveReportVo.idleTimeSum + res.rows[i].deviceLiveReportVo.offlineTimeSum + res.rows[i].deviceLiveReportVo.warnTimeSum;
            run.push(res.rows[i].deviceLiveReportVo.runTimeSum / all * 100);
            noline.push(res.rows[i].deviceLiveReportVo.offlineTimeSum / all * 100);
            free.push(res.rows[i].deviceLiveReportVo.idleTimeSum / all * 100);
            warn.push(res.rows[i].deviceLiveReportVo.warnTimeSum / all * 100);
            name.push(res.rows[i].deviceName);
          }
          this.showEcharts4(run, free, noline, warn, name);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showLineAndDev() {
      var condtion = {
        conditionJson1: [],
        current: 1,
        size: 99999999
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      condtion.conditionJson1.push({
        column: 'isProductionDevice',
        operator: 1,
        ptype: 'int',
        type: 'eq',
        value: 1
      });
      try {
        const res = await deviceRealLiveGetPageByDevice({
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var groupArr = [];
          var devJSON = [];
          // var temp = {};
          var data = res.rows;
          for (var i = 0; i < data.length; i++) {
            var lineName = '';
            if (data[i].productionLine) {
              lineName = data[i].productionLine;
            } else if (data[i].workshop) {
              lineName = data[i].workshop;
            } else if (data[i].factory) {
              lineName = data[i].factory;
            } else {
              lineName = '其他';
            }

            var index = groupArr.indexOf(lineName);
            if (index === -1) {
              groupArr.push(lineName);

              devJSON.push({
                lineName: lineName,
                devListData: [data[i]]
              });
            } else {
              devJSON[index].devListData.push(data[i]);
            }
          }

          this.htmlDevState(devJSON);
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    htmlDevState(devJSON) {
      this.devStateHtml = '';

      var sbLine = 9;
      var cnum = 75;

      var canWeb = true;
      if (window.innerWidth <= 1024) {
        sbLine = 6;
        cnum = 24;
        canWeb = false;
      } else if (window.innerWidth <= 1280) {
        sbLine = 8;
        cnum = 32;
        canWeb = false;
      }

      const height = (this.$refs.right1content.offsetHeight * 0.3) - cnum;

      var arr = devJSON;
      var html = '';

      for (var i = 0; i < arr.length; i++) {
        // 标记设备需要遍历几次
        var listNum = Math.ceil(arr[i].devListData.length / sbLine);
        // 标记第几次遍历
        var flag = 0;
        // 标记设备遍历长度
        var arrlength = 0;

        if (i !== 0) {
          var num = Math.ceil(arr[i - 1].devListData.length / sbLine);

          if (num >= 2) {
            if (canWeb) {
              html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
            } else {
              html += '<div class="lineCON" style="margin-top:' + ((num - 1) * height) + 'px;" >';
            }
            // html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
          } else {
            html += '<div class="lineCON" >';
          }
        } else {
          html += '<div class="lineCON" >';
        }

        html += '      <p class="lineName" >' + arr[i].lineName + '</p>';

        for (var j = 0; j < listNum; j++) {
          // 若设备长度与预设长度一致
          if (arr[i].devList <= sbLine) {
            arrlength += arr[i].devListData.length;
          } else {
            // 若下一次轮播设备数已经短于预设值
            if ((flag + 1) * sbLine > arr[i].devListData.length) {
              arrlength += (arr[i].devListData.length % sbLine);
            } else {
              arrlength += sbLine;
            }
          }
          flag++;
          html += '      <div class="lineDev" >';
          html += '        <div class="blackDev" >';

          for (var w = 0; w < sbLine; w++) {
            html += '       <div class="blackTask" ></div>';
          }
          html += '           <div class="blackLine" ></div>';
          html += '           </div>';
          html += '           <div class="blackState" >';
          for (var x = j * sbLine; x < arrlength; x++) {
            html += '           <div class="devTask state' + arr[i].devListData[x].deviceRealState + '" >';
            if (arr[i].devListData[x].isProductionCount === 1) {
              if (arr[i].devListData[x].currentShiftName) {
                html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].currentValue + '</span>';
              } else {
                html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].todayValue + '</span>';
              }
            } else {
              html += '          <span class="devTask_span2"  ></span>';
            }
            html += '          <p>' + arr[i].devListData[x].deviceName + '</p>';
            html += '           </div>';
          }
          html += '          </div>';
          html += '      </div>';
        }
        html += '</div>';
      }
      this.devStateHtml = html;
      // $(".right1content").html(html);
      // setTimeout(() => {
      //   this.scrollDev();
      // }, 2500);
    },
    scrollDev() {
      //     num += 1;
      //     this.$refs.right1content.scrollTo(0, num);
      //     console.log('滚动高度', this.$refs.right1content.scrollTop);
      // var num = 0;
      // setInterval(() => {
      //   this.$nextTick(() => {
      //     num += 1;
      //     this.$refs.right1content.scrollTo(0, num);
      //     console.log('滚动高度', this.$refs.right1content.scrollTop);
      //   });
      // }, 50);

      var that = this;

      var newTop;
      // var up = this.$refs.right1content.scrollTop;
      // 使用定时器
      this.timer = setInterval(() => {
        this.$nextTick(() => {
          // console.log(this.$refs.right1content.scrollTop === newTop);
          // console.log(this.$refs.right1content.scrollTop, newTop);
          if (this.$refs.right1content.scrollTop === newTop) {
            // 清除定时器

            clearInterval(this.timer);
            setTimeout(() => {
              this.showLineAndDev();
            }, 2500);
          } else {
            // 每次在原来的基础上移动
            newTop = that.$refs.right1content.scrollTop;
            this.$refs.right1content.scrollTo(0, newTop + 1);
          }
        });
      }, 50);
    },
    async showWarn() {
      if (window.innerWidth <= 1024) {
        this.size_w = 3;
      }
      var condtion = {
        current: this.current_w,
        size: this.size_w
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceEarlyWarningGetDeviceWarnRecord({
          processStateEnum: 'UN_PROCESS'
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current_w * this.size_w >= res.total) {
            this.current_w = 1;
          } else {
            this.current_w++;
          }

          this.warnList = res.rows;
          // var html = '';
          // for (var i = 0; i < res.rows.length; i++) {
