    // 名称格式化
    nameFormat(value, cutter, newline) {
      var s = value;
      var num0 = cutter;
      var num1 = cutter;
      var flag = 1; // 换行标记
      if (s.length > 2 * cutter + 1) {
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter) {
            num0 = rs[0].length;
            num1 = 2 * cutter - rs[0].length;
          }
          if (rs[1].length < cutter) {
            num0 = 2 * cutter - rs[1].length;
            num1 = rs[1].length;
          }
          rs[0] = rs[0].substring(0, num0);
          rs[1] = rs[1].substring(0, num1);
          if (rs != null) {
            s = rs.join('|');
          }
        }
      } else {
        // eslint-disable-next-line no-redeclare
        var rs = s.split('|');
        if (rs.length > 1) {
          if (rs[0].length < cutter && rs[1].length < cutter) {
            flag = 0;
          }
          if (rs != null) {
            s = rs.join('|');
          }
        }
      }
      if (newline === 1) {
        if (flag === 1) {
          s = s.substring(0, s.length / 2) + '\n' + s.substring(s.length / 2);
        } else if (flag === 0) {
          s = s.split('|').join('\n');
        }
      }
      return s;
    },
    // 看板时间
    tick() {
      var objD = new Date();
      var yy = objD.getYear();
      if (yy < 1900) yy = yy + 1900;
      var MM = objD.getMonth() + 1;
      if (MM < 10) MM = '0' + MM;
      var dd = objD.getDate();
      if (dd < 10) dd = '0' + dd;
      var hh = objD.getHours();
      if (hh < 10) hh = '0' + hh;
      var mm = objD.getMinutes();
      if (mm < 10) mm = '0' + mm;
      var ss = objD.getSeconds();
      if (ss < 10) ss = '0' + ss;
      this.time = yy + ' 年 ' + MM + ' 月 ' + dd + ' 日 ' + hh + ' : ' + mm + ' : ' + ss;
    },
    // 看板时间
    getDataTime() {
      // $.ajax({
      //   type: 'GET',
      //   url: 'http://oa.junhepumps.com:8081/idevcloudwebsub48/junheProductionTask/getPageDeviceRealInformation.action?pageNum=1&pageSize=74',
      //   data: {},
      //   dataType: 'jsonp',
      //   success: function(data) {
      //     console.log('请求成功', data);
      //   },
      //   error: function(data) {
      //     console.log('请求失败', data);
      //   },
      //   crossDomain: true,
      //   xhrFields: { withCredentials: true }
      // });
      this.dataTime = new Date($.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')).getTime();
      console.log('时间', $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date'));
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time = y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    showLogo2() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_lt2';
      } else {
        return '';
      }
    },
    showLogo3() {
      if (this.getUrlKeyMain('cid', window.location.href) === '161') {
        return 'logo_ry2';
      } else {
        return '';
      }
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(url) || [, ''])[1].replace(/\+/g, '%20')) || null;
    },
    formatDateThree(time) {
      var date = '';
      var D = '';
      date = new Date(time);
      var M = date.getMonth() < 9 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1);
      var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
      D = M + '-' + d;
      return D;
    },
    limitMoveNum() {
      let num = 0;
      this.productTaskData.forEach(item => {
        num += item.length;
      });
      return num > 2 ? 1 : 3;
    }
  }
};
</script>

